@import url(https://fonts.googleapis.com/css?family=Oswald:400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bahianita|Exo+2:400,700|Permanent+Marker|Shadows+Into+Light&display=swap);
/* CSS was organized based off semantic levels (header/main/footer), and then matched the same styles according to each section */

/********* global styles ***********/


body {
  background-image: linear-gradient(#0f3e0f, rgb(26, 80, 42), #29773E, #31864f, #063305);
  font-size: 62.5%;
}

.wrapper {
  max-width: 1400px;
  margin: 0 auto; 
  width: 85%;
}

.flex {
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  display: flex;
}

.displayNone {
  display: none;
}




/********* header begins here *************/





header {
  background-image: linear-gradient(65deg,rgba(9, 48, 9, 0.6), rgba(31, 96, 50, 0.7),rgba(41, 119, 62, 0.6), rgba(3, 34, 2, 0.8));
  margin-bottom: 1rem;
  position: relative;
  text-align: center;
  display: flex;
}

header,
.headerTitle {
  margin: 0 auto;
}

header,
.transparency {
  color: rgb(12,12,12);
  min-height: 100vh;

}

.transparency {
  background: rgba(20, 70, 35, 0.3);
  width: 100%;
  bottom: 0;
  right: 0;
  left: 0;
  top: 0;
}

.transparency,
.headerTitle,
.headerD,
.headerButton {
  position: absolute;
}

.headerTitle,
.headerD {
  text-shadow: 1px 1px 10px #fff;

}

.headerTitle {
  font-family: 'Shadows Into Light', cursive;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 8rem;
  width: 75%;
  left: 12%;
  top: 25%;
}

.headerD {
  font-size: 2rem;
  bottom: 28%;
  left: 32%;
}

.headerD,
.headerButton {
  font-family: 'Oswald', sans-serif;
  letter-spacing: 1.4px;

}

.headerButton {
  box-shadow: 3px 3px 10px #000;
  text-transform: uppercase;
  background-color: white;
  padding: 0.9rem 1.3rem;
  border: 5px solid gray;
  font-size: 1.5rem;
  bottom: 10%;
  left: 45%;
}

.headerButton:hover {
  border: 5px solid rgb(158, 156, 156);
  background-color: gray;
  transition: all 0.5s;
  box-shadow: none;
}




/************ main starts here  **************/





main {
  background-color: rgba(0,0,0,0.1);
  text-align: center;
  padding: 2rem;
}

main,
.topicButtons {
  font-family: 'Oswald', sans-serif;
}


.intro {
  text-shadow: 1px 1px 18px rgb(255, 255, 255);
  /* text-decoration: underline; */
  color: rgb(12, 12, 12);
  margin: 2.5rem auto ;
  font-size: 6rem;
  width: 62%;
  line-height: 5.5rem;
  letter-spacing: 0.5rem
}

.intro,
.topicButtons {
  text-transform: uppercase;
}

.topicButtons {
  box-shadow: 1px 1px 10px #000;
}

.hover {
  font-family: 'Shadows Into Light', cursive;
  text-shadow: 1px 1px 10px rgb(0, 0, 0);
  color:rgb(187, 163, 30);
  width: 40%;
  font-size: 2rem;
  padding: 0.5rem;
}

.hover,
.infoBox {
  margin: 0 auto;

}

.instructions {
  color: rgb(255, 255, 255);
  margin-top: 1rem;
}

.instructions,
.topicButtons {
  letter-spacing: 1.67px;
  font-size: 1.4rem;
}



  /********** section buttons  ***********/



.topicButtons {
  background-color: rgb(255, 255, 255);
  margin: 2rem 0.5rem 2rem 0.5rem;
  border: 5px solid gray;
  padding: 0.7rem 1rem;
}

.topicButtons:hover {
  border: 5px solid rgb(158, 156, 156);
  background-color: rgb(128, 128, 128);
  transition: all 0.5s;
  box-shadow: none;
}

  /****** arrow styles *****/


  .sticky {
  position: relative;
}

.arrows {
  padding-top: 0.5rem;
  position: -webkit-sticky;
  position: sticky;
  z-index: 1000;
  margin-bottom: -10rem;
  right: 0rem;
  top: 0rem;
}

.arrow1,
.arrow2 {
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: contain;
  height: 2.5rem;
  width: 2.5rem;
}

.arrow2 {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  margin-top: 0.5rem;
}

.space {
  box-shadow: 2px 2px 55px rgb(31, 31, 31);
  background-image: url(/miguel-cabrera-project-5/static/media/cork.a2f79e7e.jpg);
  border: 2rem solid rgb(12, 12, 12);
  margin-top: 4rem;
  padding: 2rem 0;
}



/********** soundboard begins here ***********/



.badSounds {
  box-shadow: 2px 2px 10px #000;
  background-repeat: no-repeat;
  background-size: 100% 300%;
  padding: 1rem 2rem;
  margin: 0 0 2rem 0;
}

.badSounds,
.characterBox {
  position: relative;
}

.badSounds,
.titleDiv {
  background-image: url(/miguel-cabrera-project-5/static/media/paper.4b332359.jpg);

} 

.mainTitle {
  font-size: 4rem;
}

.mainTitle,
.infoBox h2,
.characterInfo {
  font-family: 'Shadows Into Light', cursive;
}

.remix {
  box-shadow: 2px 2px 10px rgb(0, 0, 0);
  background-color: rgb(231, 86, 86);
  -webkit-transform: rotate(345deg);
          transform: rotate(345deg);
  padding: 0.5rem;
  right: -10%;
  top: 70%;
}

.remix,
.pin {
  position: absolute;
}





  /******** character cards / audio buttons ********/


.actorSound {
  background-image: url(/miguel-cabrera-project-5/static/media/danger.908982d1.jpg);
  background-position: 50% 110%;
  background-size: contain;
  margin: 0 0.5rem;
  cursor: pointer;
  width: 22%;
}

.actorSound,
.infoP {
  margin-bottom: 1rem;
}

.actorSound,
.infoBox h2 {
  text-align: center;
}

.actorSound:hover {
  background-color: rgb(255, 255, 255);
  transition: all 0.5s;
}

.characterBox {
  background-color: rgb(228, 225, 225);
  box-shadow: 2px 2px 10px rgb(0, 0, 0);
  list-style: none;
  cursor: pointer;
  padding: 1rem; 
}


.characterBox:hover {
  background-color: rgb(179, 177, 177);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  transition: all 0.5s;
}

.characterBox:hover .infoBox {
  transition: all 1s;
  display: block;
}

.infoBox {
  padding-bottom: 1rem;
  text-align: left;
  width: 90%;
}

.infoBox,
.characterBox:hover .pin {
  display: none;
}

.infoBox h2 {
  font-size: 1.6rem;
  margin: 1rem 0;
}

.infoBox h2,
.scrollUp {
  letter-spacing: 1.3px;
}

.infoBox h2,
.infoStats {
  font-weight: 700;
}

.infoBox h2,
.characterInfo a,
.characterInfo a:hover,
.scrollUp:hover {
  text-decoration: underline;
}

.characterInfo {
  letter-spacing: 1.8px;
}

.characterInfo,
.scrollUp {
  font-size: 1.1rem;
}

.infoBox h3,
.infoBox p {
  margin: 0.2rem 0;
  line-height: 1.4rem;
}

.infoBox h3,
.infoBox p,
img {
  width: 100%;

}

.infoP {
  flex-direction: column;
  justify-content: left;
  align-items: center;
  display: flex;
}

.infoStats {
  font-family: 'Oswald', san-serif;
  font-size: 0.8rem;
}

.infoStats,
.scrollUp {
  text-transform: uppercase;
}

.characterInfo a {
  text-decoration: none;
  color: rgb(0, 0, 0);
}
.characterInfo a:hover,
.scrollUp:hover {
  color: yellow;
}

img {
  height: 300px;
}

.pin {
  box-shadow: 1px 1px 10px rgb(0, 0, 0);
  border: 1px solid rgb(21, 11, 75);
  background-color: rgb(34, 19, 116);
  color:rgb(34, 19, 116);
  border-radius: 50%;
  padding: 0.1rem;
  left: 47%;
  top: 2%;
}

.scrollUp {
  font-size: 1.1rem;
  text-align: right;
  margin-top: 2rem;
  cursor: pointer;
  width: 95%;
}





/*********** quote section *************/


.quoteBox {
  background-color: rgb(228, 225, 225);
  box-shadow: 2px 2px 10px #000;
  margin-top: 2rem;
  padding: 2rem;
}

.quoteBox,
.quoteAnswers {
  width: 60%;
}

.quoteBox .quoteTop {
  text-transform: uppercase;
  font-size: 3rem;
}

.quoteBox .quoteTop,
.quoteBox h2,
.quoteButton {
  font-family: 'Shadows Into Light', cursive;
}

.quoteBox h2 {
  letter-spacing: 1.3px;
  font-weight: 700;
}

.quoteBox h2,
.quoteButton {
  font-size: 1.5rem;
}


.quoteBox h2,
.quoteAnswers {
  padding: 1rem;
}

.quoteQuestions {
  border: 1px solid rgb(196, 193, 193);
  background-color: rgb(255, 255, 255);
}

.quizChoices {
  display: flex;
  justify-content: space-evenly;
  margin: 1rem 0;
}

.quizChoices,
.quoteAnswers {
  font-size: 1.2rem;
}

.quoteAnswers {
  background-color: rgba(255, 255, 4, 0.432);
  margin: 2rem auto;
  font-weight: 700;
}

.quoteButton {
  letter-spacing: 1.5px;
  margin-top: 0.7rem;
  padding: 0.5rem;
}





/**********  death section ************/


.titleDiv {
  background-size: 100% 300%;
  -webkit-transform: rotate(357deg);
          transform: rotate(357deg);
  margin: 0 auto;
  padding: 1rem;
  width: 45%;
}

.titleDiv,
.BscDiv {
   box-shadow: 2px 2px 10px #000;
}

.spoilers {
  text-shadow: 2px 2px 9px #000;
  letter-spacing: 1.2px;
  font-size: 1.3rem;
  color: yellow;
}

.spoilers,
.deathInfoT {
   margin: 1rem 0;
}

.BscDiv {
  background-color: rgb(223, 223, 223);
  padding: 2rem 1rem;
  border-radius: 3%;
  margin: 0.5rem;
  width: 30%;
}

.BscDiv:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  transition: all 0.5s;
}

.deathInfo {
  margin: 0.5rem auto;
  font-size: 1.2rem;
  text-align: left;
  width: 80%;
}

.deathInfoT {
  font-size: 2rem;
}

.whoDied {
  font-family: 'Shadows Into Light', cursive;
  text-decoration: underline;
  color: red;
}






/*********** episodes section *************/


.BBDiv {
  background-color: rgb(242, 242, 125);
}

.BBDiv2 {
  background-color: rgba(187, 233, 241, 0.904);
}

.BBDiv,
.BBDiv2 {
  margin: 0.5rem;
  padding: 1rem;
  width: 30%;
}

.BBDiv2:hover,
.BBDiv:hover {
  -webkit-transform: rotate(349deg);
          transform: rotate(349deg);
}

.BBDiv,
.BBDiv2,
.showButtons {
  box-shadow: 3px 3px 10px #000;
}

.showButtons {
  background-color: rgb(255, 255, 255);
  margin: 2rem 0.5rem 1rem 0.5rem;
  text-transform: uppercase;
  border: 2px solid gray;
  padding: 0.5rem 0.8rem;
  letter-spacing: 1.5px;
  border-radius: 20px;
  font-size: 1.3rem;
}

.showButtons,
.infoEp {
   font-weight: 700;
}

.showButtons,
.infoEp,
.showTitle {
   font-family: 'Shadows Into Light', cursive;
}

.showButtons:hover {
  background-color: rgb(148, 148, 148);
}

.infoEp {
  margin: 1rem auto;
  color: red;
}
.infoShow {
  text-align: left;
  margin: 0.5rem 0;
}

.showTitle {
  letter-spacing: 1.4px;
}

.infoEp,
.infoShow,
.showTitle {
  font-size: 1.5rem;
}





/********* review box section ************/


.reviewBox {
  background-color: rgb(255, 255, 255);
  box-shadow: 2px 2px 20px #000;
  margin: 8.5rem auto;
  padding: 2rem;
  z-index: 1;
}

.reviewBox,
.reviewInput {
  width: 80%; 
}

.reviewTitle {
  font-size: 2rem;
}

.reviewTitle,
.reviewSubmit {
  font-family: 'Shadows Into Light', cursive;
}

.reviewComp {
  border: 1px solid rgb(177, 176, 176);
  overflow-y: scroll;
  max-height: 500px;
  padding: 3rem;
}

.reviewSubmit {
  letter-spacing: 1.2px;
  margin-top: 1rem;
}

.singleReview {
  background-color: rgba(140, 201, 164, 0.524);
  border-radius: 10px;
  margin: 0.2rem auto;
  position: relative;
  line-height: 1.2rem;
  padding: 0.7rem;
}

.reviewInput,
.reviewSubmit {
  padding: 1rem ;
}

.singleReview:hover::before {
  position: absolute;
  content: 'X';
  left: 1rem;
  top: 1rem;
}


.singleReview span {
  display: inline-block;
  text-align: left;
  margin: 0.5rem;
  width: 90%;
}






/********* aside begins here *************/


ul {
  list-style: none;
}

.nav {
  margin: 5rem 0 3rem 0;
  font-size: 1.5rem;
}

.nav ul {
  justify-content: space-between;
}

.nav ul a {
  font-family: 'Shadows Into Light', cursive;
  color:rgb(204, 179, 35);
  text-decoration: none;
  letter-spacing: 2px;
  text-shadow: 1px 1px 5px #000;
}

.nav ul a:hover {
  color: gray;
}

.credits {
  font-weight: 400;
  margin: 0 auto;
  color: #fff;
  width: 60%;
  line-height: 1.2rem;
  letter-spacing: 1.2px;
}

.credits,
.copyright p {
  font-size: 1rem;
}






/************ footer begins here ***********/


.copyright {
  background-color: #000;
  text-align: center;
}

.copyright p {
  /* font-family: 'Oswald', sans-serif; */
  letter-spacing: 3px;
  padding: 2rem 0;
  color: white;
  margin: 0;
   font-family: 'Exo 2', sans-serif;
  /* font-family: 'Bahianita', cursive; */
  /* font-family: 'Permanent Marker', cursive;  */
}



/* my styles end here  */




.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

