   }
   .showButtons {
      font-size: 0.9rem;
      letter-spacing: 1.1px;
   }
   .infoEp,
   .infoShow,
   .showTitle {
      font-size: 0.95rem;
   }

   
   /* review box styles  */

   .reviewComp {
      padding: 1.3rem;
   }
   .reviewInput {
