   }
}



/******** 430 px  ********/


@media (max-width: 430px) {

   /* main styles  */
   
   .intro {
      font-size: 2.4rem;
      line-height: 2rem
   }
   .hover {
      width: 85%;
   }
   .instructions {
      width: 95%;
   }
   .arrows {
      margin-left: 2rem;
      margin-bottom: -9rem;
   }
   .space {
      border: 0.7rem solid #000;
   }

