   .badSounds {
      padding: 0.8rem 1.4rem;
   }
   .actorSound {
      width: 45%;
   }

   /* season section styles */

   .mainTitle {
      font-size: 2.5rem;
   }
   .showButtons {
      font-size: 0.7rem;
      letter-spacing: 1.3px;
   }

   /* death section styles */
   .BscDiv {
      margin: 0.2rem;
   }
   .BscDiv:hover {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
   }
   
   /* quote section styles */
   
   .quoteBox {
      padding: 1rem;
   }
   .quoteBox .quoteTop {
      font-size: 2rem;
   }
   .quoteBox h2 {
      font-size: 1.1rem;
   }

   
}


/******** 400 px  ********/



@media (max-width: 400px) {
   .wrapper {
      width: 100%;
   }
   /* header styles */
   
   .headerTitle {
      font-size: 4.5rem;
      top: 20%;
   }
   .headerButton {
      left: 34%;
   }
   .headerD {
      bottom: 32%;
   }
   .headerButton {
      bottom: 12%;
      font-size: 1.1rem;
      padding: 0.7rem 1rem;
   }

   /* main styles */

   main {
      padding: 0.5rem;
   }
   
   .intro {
      font-size: 2rem;
      width: 100%;
   }
   .hover {
      font-size: 1rem;
   }
   .instructions {
      font-size: 0.9rem;
   }
   .topicButtons {
      font-size: 0.8rem;
      padding: 0.3rem 0.7rem;
      letter-spacing: 1.4px;
      margin: 1.2rem 0.2rem 4rem 0.2rem;
   }
   .arrows {
      margin-bottom: -8rem;
      margin-left: 1rem;
   }
   .arrow1,
   .arrow2 {
      width: 1.5rem;
      height: 1.5rem;
   }
   .mainTitle {
      font-size: 2rem;
   }
   .actorSound {
      width: 44%;
      margin: 0.3rem;
   }
   .characterBox {
      padding: 0.5rem;
   }
   img {
      height: 150px;
   }
   .infoBox h2 {
      font-size: 1.2rem;
   }
   .infoBox h3 {
      font-size: 1rem;
   }
   .infoBox h3,
   .infoBox p {
      line-height: 1.2rem;
      margin: 0.15rem 0;
   }
   .characterInfo {
      font-size: 0.9rem;
   }
   .infoStats {
      letter-spacing: 3px;
   }
   .pin {
      padding: 0.005rem;
      top: 1%;
   }

   /* season section styles */

   .showButtons {
      margin: 2rem 0.3rem 1rem 0.3rem;
      letter-spacing: 1px;
      padding: 0.3rem 0.6rem;
   }
   .BBDiv,
   .BBDiv2 {
      margin: 0.3rem;
   }

   /* review box styles */
   .reviewBox {
      padding: 1rem;
   }
   .reviewTitle {
      margin: 0.7rem 0;
   }
   
   /* footer styles */
   .copyright p {
      font-size: 0.7rem;
      letter-spacing: 2px;
   }


}


@media (max-width: 360px) {
   .reviewBox {
      padding: 1.3rem;
   }
   .reviewInput {
      font-size: 0.7rem;
   }
}
